.home-cards {
    .col-md-4 {
        @include mobile {
            margin-bottom: 15px;
        }

    }
}

.card {
    border-radius: 10px;
    // background-color: $yash;
    text-align: center;
    height: 100%;
    box-shadow: 2px 2px 6px 1px #e5e3e3;
    overflow: hidden;
    transition: all 1s;

    @include mobile {
        margin-bottom: 20px
    }

    &:hover {
        
        &:hover {
            transition: all 1s;
            img {
                transition: all 1s;
                transform: rotateY(180deg);
            }
            
        }

    }

    img {
        width: 100%;
        max-width: 100px;
        transition: all 1s;
        margin-bottom: 15px;
        margin-top: 10px;

     
    }

    .card-body {
        background-color: $white;
        border-bottom: 1px solid #e5e3e3;


        p {
            max-height: 180px;
            overflow: hidden;
            margin-bottom: 0
        }
    }

    h3 {
        color: $blue;
    }

}